import React from 'react'

export const Navbar = () => {
    return (
        <div className='navbar navbar-dark bg-dark mb-4 px-4'>
            <span className='navbar-brand'>
                Momoy
            </span>

            <button className='btn btn-outline-warning'>
                <i className='fas fa-sign-out-alt'></i> 
                <span>Salir</span> 
            </button>
        </div>
    )
}
